<template>
	<view class="uv-page">
		<uv-form
			:labelPosition="formLabelPosition"
			:labelAlign="formLabelAlign"
			:labelStyle="formLabelStyle"
			:labelWidth="formLabelWidth"
			:errorStyle="errorStyle"
			:ref="formRef"
		>
			<uv-form-item
				v-for="item in optionList"
				:key="item.prop"
				:label="item.label"
				:labelPosition="item.labelPosition ? item.labelPosition : formLabelAlign"
				:borderBottom="getBorderBottom(item)"
				:customStyle="formItemCustomStyle"
				:labelWidth="formItemLabelWidth"
			>
				<view class="box" :style="getCellBoxStyle(item)">
					<view v-if="item.type === 'image'" class="image-box" :style="imageBoxStyle">
						<image
							v-for="(citem, cindex) in item.value"
							:key="cindex"
							:index="cindex"
							:src="citem"
							mode="aspectFill"
							class="show-image"
						></image>
					</view>
					<view v-else>
						{{ item.value }}
					</view>
				</view>
			</uv-form-item>
		</uv-form>
	</view>
</template>

<script>
import ImgUpload from "@/components/upload/imgs.vue";
import props from "./paFormProps.js";
export default {
	mixins: [props],
	components: {
		ImgUpload
	},
	data() {
		return {
			baseModel: {},
			formRef: "ref" + new Date().getTime()
		};
	},
	computed: {
		imageBoxStyle() {
			let styleObj = {};
			if (this.inputAlign == "right") {
				styleObj["justify-content"] = "flex-end";
			}
			if (this.inputAlign == "center") {
				styleObj["justify-content"] = "center";
			}
			if (this.inputAlign == "left") {
				styleObj["justify-content"] = "flex-start";
			}
			return styleObj;
		}
	},
	methods: {
		getCellBoxStyle(item) {
			if (item.labelPosition == "top") {
				return {};
			}
			let styleObj = {
				textAlign: this.inputAlign ? this.inputAlign : "right"
			};
			return styleObj;
		},
		getBorderBottom(item) {
			if (Object.hasOwn(item, "borderBottom")) {
				return item.borderBottom;
			}
			return this.borderBottom;
		}
	}
};
</script>

<style lang="scss" scoped>
.show-image {
	display: block;
	width: 160rpx;
	height: 160rpx;
}
.image-box {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: 16rpx;
}
</style>
